@import url(https://fonts.googleapis.com/css2?family=Ultra&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

.App {
  font-family: "Ultra", serif;
}

.App .header {
  display: block;
  position: fixed;
  width: 100%;
  height: 10vh;
  background-color: transparent;
}

.App .header #navbar {
  opacity: 0;
  position: relative;
  height: 100%;
  font-size: 1.5vw;
}

.App .header #navbar .nav-items {
  display: flex;
  justify-content: space-around;
  align-items: center;
  float: right;
  width: 25%;
  height: 100%;
  list-style-type: none;
}

.App .header #navbar .nav-links {
  color: whitesmoke;
  text-decoration: none;
}

.App .header #navbar .nav-links:hover {
  color: grey;
}

.App #welcome-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url("https://i.redd.it/jfysz0ok2tj21.jpg");
  text-align: center;
  font-family: "Lobster", cursive;
}

.App #welcome-section .Intro {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.App #welcome-section .Intro .intro-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  background-image: linear-gradient(62deg, #000000 0%, #252525 100%);
}

.App #welcome-section .Intro .intro-div .intro-txt {
  opacity: 0;
}

.App #welcome-section .Intro .intro-div .intro-txt .intro-text {
  color: whitesmoke;
  font-size: 4rem;
}

.App #welcome-section .Intro .slider {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #333333;
  background-image: linear-gradient(62deg, #383838 0%, #474747 100%);
  transform: translateY(100%);
}

.App #welcome-section .Intro .welcome-text {
  color: whitesmoke;
  font-size: 3rem;
  cursor: default;
  text-decoration: none;
}

.App #welcome-section .Intro #welcome-link {
  color: whitesmoke;
  text-decoration: none;
}

.App #welcome-section .Intro #welcome-link:hover {
  color: #349997;
  text-decoration: underline;
  cursor: pointer;
}

.App #welcome-section .Intro .main-page {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 50%;
  background-color: red;
}

.App #welcome-section .Intro .main-page .my-info {
  width: 100%;
  height: 80%;
  background-color: blue;
}

.App #projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 4rem solid black;
  background-image: linear-gradient(62deg, #0d4846 0%, #349997 100%);
  /* Hide scrollbar for Chrome, Safari and Opera */
  /* Add Zoom Animation */
  /* The Close Button (x) */
}

.App #projects #project-title {
  text-align: center;
  padding: 5vh 0;
  color: black;
  font-size: 4rem;
  font-weight: 400;
}

.App #projects .project-tile {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  max-width: 1024px;
  width: 100%;
  height: 50%;
  margin: 2.5rem 0 7.5rem 0;
  background-color: whitesmoke;
  border: 0.5rem solid black;
  outline: 0.25rem solid whitesmoke;
}

.App #projects .categories {
  width: 328px;
  height: 328px;
  margin: 0 1.25px;
  padding-bottom: 350px;
  background-color: whitesmoke;
  border: 1rem solid whitesmoke;
  text-align: center;
}

.App #projects .categories .icon {
  cursor: pointer;
  border-radius: 50%;
}

.App #projects .categories:hover {
  background-color: #c8c8c8;
}

.App #projects #popup-window {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
  z-index: 3;
}

.App #projects #popup-content {
  width: 90%;
  height: 90%;
}

.App #projects .popup-projects {
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: scroll;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.App #projects .popup-projects .category-title {
  margin: 2vw;
  text-align: center;
  font-size: 3vw;
}

.App #projects .popup-projects::-webkit-scrollbar {
  display: none;
}

.App #projects .Project {
  display: flex;
  flex-direction: row;
  width: 90%;
  margin: 2.5vh 0 5vh 0;
  padding: 2vh;
  border: 4px solid black;
  background-color: whitesmoke;
}

.App #projects .Project .project-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
}

.App #projects .Project .info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  width: 50%;
  height: 100%;
}

.App #projects .Project .info .tags {
  list-style: none;
  margin: 0;
  overflow: hidden;
  padding: 0;
}

.App #projects .Project .info .tags li {
  float: left;
}

.App #projects .Project .info .tag {
  background: black;
  border-radius: 25%;
  color: #fff;
  display: inline-block;
  height: 26px;
  line-height: 26px;
  padding: 0 20px 0 23px;
  position: relative;
  margin: 0 10px 10px 0;
  text-decoration: none;
  transition: color 0.2s;
}

.App #projects .Project .info .tag::before {
  background: #fff;
  border-radius: 10px;
  box-shadow: inset 0 1px rgba(0, 0, 0, 0.25);
  content: "";
  height: 6px;
  left: 10px;
  position: absolute;
  width: 6px;
  top: 10px;
}

.App #projects .Project .info .tag:hover {
  background-color: crimson;
  color: white;
}

.App #projects .Project .info .tag:hover::after {
  border-left-color: crimson;
}

.App #projects .Project .info .gitRepo,
.App #projects .Project .info .gitPage {
  font-size: 3.5rem;
  color: #000;
  margin: 1rem;
}

.App #projects .Project .info .gitRepo:hover,
.App #projects .Project .info .gitPage:hover {
  color: grey;
}

.App #projects .animate {
  -webkit-animation: animatezoom 0.6s;
  animation: animatezoom 0.6s;
}

@-webkit-keyframes animatezoom {
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}

@keyframes animatezoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

.App #projects #close {
  position: absolute;
  top: 0;
  right: 25px;
  color: #fff;
  font-size: 5vw;
  font-weight: bold;
}

.App #projects #close:hover,
.App #projects #close:focus {
  color: #349997;
  cursor: pointer;
}

.App .view-codepen {
  color: black;
  text-decoration: none;
  font-size: 1.5vw;
  margin: 2.5vh;
}

.App .view-codepen:hover {
  color: #b4b4b4;
}

.App .Footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 75vh;
  background: #73c8a9;
  /* fallback for old browsers */
  background: linear-gradient(360deg, #000000 0%, #0d4846 100%);
  color: #afafaf;
}

.App .Footer .footer-title {
  font-size: 3.5vw;
  margin: 5vw;
}

.App .Footer .footer-info {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75%;
  height: 80%;
}

.App .Footer .footer-info .location,
.App .Footer .footer-info .contact-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  height: 100%;
}

.App .Footer .footer-info .location .contact-links,
.App .Footer .footer-info .contact-info .contact-links {
  width: 4.5rem;
  height: 4.5rem;
  margin: 1rem;
  font-size: 3rem;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  border: 1px solid black;
  color: black;
}

.App .Footer .footer-info .location .contact-links:hover,
.App .Footer .footer-info .contact-info .contact-links:hover {
  background-color: black;
  color: whitesmoke;
}

.App .Footer .copyright {
  font-size: 1vw;
  margin: 2vw;
}

@media only screen and (max-width: 768px) {
  #navbar {
    font-size: 6vw;
  }
  .nav-items {
    width: 50vw;
  }
  .project-tile {
    display: flex;
    flex-flow: column wrap;
    background-color: whitesmoke;
  }
  .categories {
    margin: 15px 0;
  }
}
